import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useCallback, useEffect, useState } from "react";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useInView } from "react-intersection-observer";
import { customDebounce } from "../../../Utils/Functions/debounce";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { useAdmissionExcessFeeRefundedList } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import ExcessRefundListCard from "./ExcessRefundListCard";

const ExcessRefundList = ({ aid, totalRefunded }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    admissionExcessFeeRefundedList,
    admissionExcessFeeRefundedListLoading,
  } = useAdmissionExcessFeeRefundedList({
    data: {
      aid: aid,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !aid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionExcessFeeRefundedList?.list?.length) {
      let obj = nextPage(admissionExcessFeeRefundedList?.list?.length);
      setState(() => obj);
    }
  }, [admissionExcessFeeRefundedList?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  return (
    <>
      <div className={style.fm_header_container}>
        <h6>
          {t("total_refund")} {" : "} {t("rs_only")} {totalRefunded ?? 0}
        </h6>
        <div className={style.fm_search}>
          <section className={style.fm_search_container}>
            <div className={style.fm_search_container_inner}>
              <img
                className={style.fm_search_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
              />
            </div>
          </section>
        </div>
      </div>

      {admissionExcessFeeRefundedList?.list?.map((history, index) =>
        admissionExcessFeeRefundedList?.list?.length === index + 1 ? (
          <div key={history?._id} ref={ref}>
            <ExcessRefundListCard history={history} />
          </div>
        ) : (
          <ExcessRefundListCard history={history} key={history?._id} />
        )
      )}

      {admissionExcessFeeRefundedListLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        admissionExcessFeeRefundedList?.list?.length === 0 && (
          <EmptyMessage title="No students found related this search." />
        )
      ) : (
        admissionExcessFeeRefundedList?.list?.length === 0 && (
          <EmptyMessage title="No students." />
        )
      )}
    </>
  );
};

export default ExcessRefundList;
