import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useState } from "react";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import HorizontalTabWrapper from "../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../Ui/Tab/Horizontal/HorizontalTabCard";
import ExcessFeesList from "./ExcessFeesList";
import ExcessRefundList from "./ExcessRefundList";
import {
  useAdmissionExcessFeeNotRefundedListExport,
  useAdmissionExcessFeeRefundedListExport,
} from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import QvipleLoader from "../../../Loader/QvipleLoader";
const ExcessFees = ({ totalRefund, aid, totalRefunded }) => {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [disabled, setDisabled] = useState(false);
  const [admissionExcessFeeNotRefundedListExport] =
    useAdmissionExcessFeeNotRefundedListExport();
  const [admissionExcessFeeRefundedListExport] =
    useAdmissionExcessFeeRefundedListExport();

  const onExportExcel = () => {
    if (aid) {
      setDisabled((pre) => !pre);

      if (searchParams.get("n") === "refunded") {
        admissionExcessFeeRefundedListExport({
          aid: aid,
        })
          .then(() => {
            setDisabled((pre) => !pre);
            let url =
              getQuery.state?.urlOffSet === "Institute"
                ? `/q/${params.username}/institute/member/finance?type=export`
                : `/q/${params.username}/member/staff/finance?type=export`;
            navigate(url, {
              state: getQuery.state,
            });
          })
          .catch({});
      } else {
        admissionExcessFeeNotRefundedListExport({
          aid: aid,
        })
          .then(() => {
            setDisabled((pre) => !pre);
            let url =
              getQuery.state?.urlOffSet === "Institute"
                ? `/q/${params.username}/institute/member/finance?type=export`
                : `/q/${params.username}/member/staff/finance?type=export`;
            navigate(url, {
              state: getQuery.state,
            });
          })
          .catch({});
      }
    }
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("excess_fees_refund")}</h6>
            <div className={style.fm_both_filter}>
              <div
                onClick={onExportExcel}
                className={style.fm_both_filter_with_name}
              >
                <img
                  src={`${assestsUserFinanceUrl}/export.svg`}
                  alt="filter icon"
                  style={{
                    width: "2rem",
                    height: "2rem",
                  }}
                  title="Export In Excel"
                />
              </div>
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />

        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <HorizontalTabWrapper>
            {tabList.map((tab) => (
              <HorizontalTabCard
                key={tab?.id}
                tab={tab}
                isLink
                isActive={
                  tab.id === "excess_fee" && !searchParams.get("n")
                    ? true
                    : searchParams.get("n") === tab.id
                    ? true
                    : false
                }
                parentState={getQuery.state}
                url={
                  getQuery.state?.urlOffSet === "Institute"
                    ? `/q/${params.username}/institute/member/finance?type=excess${tab.urlKey}`
                    : `/q/${params.username}/member/staff/finance?type=excess${tab.urlKey}`
                }
              />
            ))}
          </HorizontalTabWrapper>
          <BorderBottom
            customStyleBorder={{
              marginTop: "0",
            }}
          />
        </UserMemberContentWrapper>
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          {searchParams.get("n") === "refunded" ? (
            <ExcessRefundList aid={aid} totalRefunded={totalRefunded} />
          ) : (
            <ExcessFeesList aid={aid} totalRefund={totalRefund} />
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default ExcessFees;

const tabList = [
  {
    label: "excess_fee",
    id: "excess_fee",
    urlKey: "",
  },
  {
    label: "refunded",
    id: "refunded",
    urlKey: "&n=refunded",
  },
];
