import style from "../../FinanceManager/PendingFees/PendingFees.module.css";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import { assestsFeedUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useNavigate, useParams } from "react-router-dom";
import { useTranslation } from "react-i18next";
const ExcessFeesListCard = ({ history }) => {
  const { t } = useTranslation();
  const params = useParams();
  const navigate = useNavigate();

  const onViewApplicantProfile = () => {
    navigate(`/q/${params.username}/member/student/admission/profile`, {
      state: {
        // studentId: student?._id
        sid: history?.student?._id,
        viewAs: "ADMISSION_ADMIN",
        collectAccess: "ADMISSION_ADMIN",
      },
    });
  };

  return (
    <div
      className={style.pending_fees_card}
      onClick={onViewApplicantProfile}
      title="Refund student fees"
    >
      <div className={style.pending_fees_card_container}>
        <img
          alt="Student Avatar"
          src={
            history?.student?.studentProfilePhoto
              ? `${imageShowUrl}/${history?.student?.studentProfilePhoto}`
              : `${assestsFeedUrl}/feed-user-avatar.svg`
          }
          loading="lazy"
        />
        <div className={style.pending_fees_card_inner}>
          <h6>{`${history?.student?.studentFirstName} ${
            history?.student?.studentMiddleName
              ? history?.student?.studentMiddleName
              : ""
          } ${history?.student?.studentLastName}`}</h6>

          <p>
            {t("gr_number")}
            {" : "}
            {history?.student?.studentGRNO ?? ""}
            {history?.student?.department?.dName
              ? `, ${history?.student?.department?.dName}`
              : ""}
          </p>
        </div>
      </div>
      <div className={style.pending_fees_card_inner}>
        <h6>
          {t("rs_only")} {history?.refund ?? 0}
        </h6>
        <p>{t("excess_fees")}</p>
      </div>
    </div>
  );
};

export default ExcessFeesListCard;
